import AuthForm from "../components/AuthForm";

export default function Profile() {
  const isAuth = false;
  if (!isAuth) return (
    <>
      <AuthForm
        title="Zarejestruj się"
      />
      <AuthForm 
        title="Zaloguj się"
      />
    </>
  );

  return (
    <div>Profil</div>
  )
}
